                <input\n                                type=\"text\"\n                                id=\"name\"\n                                name=\"name\"\n                                v-model=\"contactForm.name\"\n                                placeholder=\"Full Name\"\n                            />\n                            <span v-if=\"errors && errors.name\" class=\"errMsg\">{{\n                                errors.name[0]\n                            }}</span>\n                        </label>\n                        <label for=\"cname\">\n                            <input\n                                type=\"text\"\n                                id=\"cname\"\n                                name=\"cname\"\n                                v-model=\"contactForm.companyName\"\n                                placeholder=\"Company Name\"\n                            />\n                            <span\n                                v-if=\"errors && errors.companyName\"\n                                class=\"errMsg\"\n                                >{{ errors.companyName[0] }}</span\n                            >\n                        </label>\n                        <label for=\"email\">\n                            <input\n                                type=\"email\"\n                                id=\"email\"\n                                name=\"email\"\n                                v-model=\"contactForm.email\"\n                                placeholder=\"Enter a Valid E-mail ID\"\n                            />\n                            <span\n                                v-if=\"errors && errors.email\"\n                                class=\"errMsg\"\n                                >{{ errors.email[0] }}</span\n                            >\n                        </label>\n                        <label for=\"mobile\">\n                            <input\n                                type=\"text\"\n                                id=\"mobile\"\n                                name=\"mobile\"\n                                v-model=\"contactForm.mobile\"\n                                placeholder=\"Mobile Number\"\n                            />\n                            <span\n                                v-if=\"errors && errors.mobile\"\n                                class=\"errMsg\"\n                                >{{ errors.mobile[0] }}</span\n                            >\n                        </label>\n                        <select\n                            v-model=\"contactForm.reason\"\n                            name=\"reason\"\n                            id=\"reason\"\n                        >\n                            <option disabled value=\"\">What’s this about</option>\n                            <option value=\"vendor\">To Be A Vendor</option>\n                            <option value=\"advertise\">To Advertise</option>\n                            <option value=\"demo\">To Have A Demo</option>\n                            <option value=\"participate\">\n                                Participate With Us\n                            </option>\n                            <option value=\"feedback\">Feedback</option>\n                        </select>\n                        <label for=\"message\">\n                            <textarea\n                                name=\"message\"\n                                id=\"message\"\n                                cols=\"30\"\n                                rows=\"10\"\n                                v-model=\"contactForm.message\"\n                                placeholder=\"Kindly supply additional details\"\n                            ></textarea>\n                            <span\n                                v-if=\"errors && errors.message\"\n                                class=\"errMsg\"\n                                >{{ errors.message[0] }}</span\n                            >\n                        </label>\n                        <button :disabled=\"disableForm\" type=\"submit\">\n                            Submit\n                        </button>\n                    </form>\n                </div>\n                <div style=\"width: 48%\" id=\"right-section\">\n                    <img src=\"/assets/defaults/about_animated.png\" alt=\"\" />\n                    <h1>Get in touch</h1>\n                    <h2>we are here for you! How can we help?</h2>\n                    <h3>HAPPYWED</h3>\n                    <!-- <div>\n                        <img src=\"/assets/defaults/ci_location.svg\" alt=\"\" />\n                        <span\n                            >No:5,1st floor, Damodar Street, Kellys, Kilpauk,\n                            Chennai, Tamil Nadu 600010</span\n                        >\n                    </div> -->\n                    <div>\n                        <img\n                            src=\"/assets/defaults/fluent_call-20-filled.svg\"\n                            alt=\"\"\n                        />\n                        <a href=\"tel:+919840785986\">+91 98407 85986 </a>\n                    </div>\n                    <div>\n                        <img\n                            src=\"/assets/defaults/clarity_email-solid.svg\"\n                            alt=\"\"\n                        />\n                        <a href=\"mailto:happywed22@gmail.com\"\n                            >happywed22@gmail.com</a\n                        >\n                    </div>\n                </div>\n                <!-- <div class=\"links\">\n                    <div>\n                        <a\n                            href=\"https://www.facebook.com/Happy-Wed-102589325754190/\"\n                            ><i class=\"fab fa-facebook-f\"></i\n                        ></a>\n                        <a href=\"https://twitter.com/HappyWed2?s=09\"\n                            ><i class=\"fab fa-twitter\"></i\n                        ></a>\n                        <a\n                            href=\"https://youtube.com/channel/UCcBPjhZHz_jOb1M_Nvk0mNA\"\n                            ><i class=\"fab fa-youtube\"></i\n                        ></a>\n                        <a href=\"\"><i class=\"fab fa-instagram\"></i></a>\n                    </div>\n                </div> -->\n            </div>\n        </div>\n        <flashMessage :message=\"message\"></flashMessage>\n    </div>\n</template>\n\n<script>\nimport flashMessage from \"../FlashMessage.vue\";\n\nexport default {\n    components: {\n        flashMessage,\n    },\n    data() {\n        return {\n            contactForm: {\n                name: \"\",\n                email: \"\",\n                message: \"\",\n                companyName: \"\",\n                reason: \"\",\n                mobile: \"\",\n            },\n            message: null,\n            errors: null,\n            disableForm: false,\n        };\n    },\n    methods: {\n        contactSubmit(e) {\n            let _this = this;\n            _this.disableForm = true;\n            let formData = new FormData();\n            let data = JSON.stringify(_this.contactForm);\n            formData.append(\"data\", data);\n            axios\n                .post(\"/api/contactus\", formData)\n                .then(function (response) {\n                    _this.message = \"Query Submitted!!\";\n                    _this.disableForm = false;\n                    _this.errors = null;\n                    setTimeout(function () {\n                        _this.message = null;\n                    }, 3000);\n                    _this.contactForm.name = \"\";\n                    _this.contactForm.email = \"\";\n                    _this.contactForm.message = \"\";\n                    _this.contactForm.companyName = \"\";\n                    _this.contactForm.reason = \"\";\n                    _this.contactForm.mobile = \"\";\n                })\n                .catch((error) => {\n                    _this.errors = error.response.data.errors;\n                    _this.disableForm = false;\n                    //console.log(error.response.data.errors);\n                    _this.message = \"Something went wrong!!\";\n                    setTimeout(function () {\n                        _this.message = null;\n                    }, 3000);\n                });\n        },\n    },\n};\n</script>\n\n<style scoped>\n.errMsg {\n    color: red;\n    font-size: 10px;\n}\n.links {\n    width: 2%;\n}\n.links > div {\n    position: absolute;\n    background: #461952;\n    border-radius: 3px 3px 20px 0px;\n    bottom: 0;\n    display: flex;\n    flex-direction: column;\n    padding: 1em;\n    right: -3%;\n}\n.links > div > a {\n    text-decoration: none;\n    color: white;\n}\n.links > div > a > i {\n    margin-bottom: 1.5em;\n    color: white;\n}\n.back-img-cont {\n    position: relative;\n}\n.back-img-cont > img:nth-child(1) {\n    width: 100%;\n    object-fit: cover;\n}\n.back-img-cont > img:nth-child(2) {\n    position: absolute;\n    left: 5%;\n    bottom: -2%;\n}\n.back-img-cont > img:nth-child(3) {\n    position: absolute;\n    right: 0%;\n    bottom: 20%;\n}\n.about-form {\n    background: #ffffff;\n    border-radius: 20px;\n    display: flex;\n    flex-wrap: wrap;\n    justify-content: space-between;\n    flex-direction: row-reverse;\n    position: absolute;\n    top: 5%;\n    left: 15.5%;\n    padding: 3em 4em;\n    width: 70%;\n}\n#right-section > h1 {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 600;\n    font-size: 32.8942px;\n    line-height: 49px;\n    color: #461952;\n    margin-bottom: 10px;\n}\n#right-section > h2 {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 500;\n    font-size: 16px;\n    line-height: 24px;\n    color: #000000;\n    margin-bottom: 5px;\n}\n#right-section > h3 {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 600;\n    font-size: 17.1627px;\n    line-height: 26px;\n    color: #461952;\n    margin-bottom: 30px;\n}\n#right-section > div {\n    margin-bottom: 1em;\n}\n#right-section > div > a {\n    text-decoration: none;\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 800;\n    font-size: 12px;\n    line-height: 18px;\n\n    color: #000000;\n}\n#right-section > div > img {\n    margin-right: 1.2em;\n}\n.contact-form {\n    padding: 2em;\n}\n.contact-form > h1 {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 600;\n    font-size: 21.8938px;\n    line-height: 31px;\n    color: #461952;\n}\n.contact-form > h3 {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 500;\n    font-size: 14px;\n    line-height: 21px;\n    color: #000000;\n}\n.contact-form label,\n.contact-form select {\n    width: 100%;\n    margin-bottom: 1em;\n}\n.contact-form input,\n.contact-form select,\ntextarea {\n    width: 100%;\n    background-color: #f5f4fa !important;\n    border-radius: 3px;\n    border: none;\n    padding: 1em;\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 500;\n    font-size: 12.33333px;\n    line-height: 14px;\n    color: #404040;\n}\n\n.contact-form input::placeholder,\n.contact-form option,\ntextarea::placeholder {\n    font-family: \"Poppins\" !important;\n    font-style: normal !important;\n    font-weight: 500 !important;\n    font-size: 12.33333px I !important;\n    line-height: 14px !important;\n    color: #a8a8a8 !important;\n}\n.contact-form button {\n    background: #461952;\n    border-radius: 3px;\n    border: none;\n    outline: none;\n    text-align: center;\n    width: 100%;\n    padding: 1em;\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 700;\n    font-size: 12.33333px;\n    line-height: 14px;\n    color: #ffffff;\n}\n@media only screen and (max-width: 992px) {\n    .about-form {\n        padding: 20px;\n    }\n}\n@media only screen and (max-width: 768px) {\n    .about-form {\n        width: 95%;\n    }\n    .about-form {\n        left: 10px;\n    }\n}\n@media only screen and (max-width: 992px) {\n    .about-form > div {\n        width: 100% !important;\n        text-align: center;\n    }\n    #outer-cont {\n        height: 1200px !important;\n    }\n}\n</style>\n"],"sourceRoot":""}]);
